import chalk from 'chalk';
import ShortUUID from 'short-uuid';

export interface DispatchOptions {
    panel: string,
    action: string,
    data?: Record<string, any>,
    response?: (data: Record<string, any>) => void,
}

export default class Panel {
    id = '';
    isReady = false;
    rpcList: Record<string, any> = {};
    // 后续这个url可以向编辑器要，这样保证准确性
    private url = 'ws://localhost:2022';
    private ws: WebSocket | null = null;
    public messages: Record<string, any> = [];

    constructor(panelID: string) {
        this.id = panelID;
    }

    private onMessage(event: any) {
        console.log(chalk.yellow('message', event.data));
        let socketData = JSON.parse(event.data);
        let { action, data, rpc, from, to } = socketData;
        const rpcCall = this.rpcList[rpc];
        if (rpcCall) {
            try {
                const jsonData = JSON.parse(data);
                rpcCall(jsonData);
                delete this.rpcList[rpc];
            } catch (e: any) {
                console.error(e);
            }
        } else {
            console.warn('invalid action', action);
        }
    }

    private _sendMessage(data: Object) {
        if (this.ws) {
            this.ws.send(JSON.stringify(data));
        } else {
            console.warn('Not connected to server, can not send data');
        }
    }

    dispatch(opts: DispatchOptions) {
        const rpc = ShortUUID.generate();
        const sendData = {
            from: this.id,
            to: opts.panel,
            rpc,
            type: 'dispatch',
            action: opts.action,
            data: JSON.stringify(opts.data || {}),
        };
        this.rpcList[rpc] = opts.response || ((data: Record<string, any>) => {
        });
        this._sendMessage(sendData);
    }

    register() {
        this._sendMessage({
            type: 'register',
            to: this.id,
        });
    }

    async init() {
        return new Promise(((resolve, reject) => {
            let ws = new WebSocket(this.url);
            ws.onopen = () => {
                console.log('open');
                this.isReady = true;
                this.register();
                resolve(0);
            };
            ws.onmessage = this.onMessage.bind(this);
            ws.onerror = () => {
                this.isReady = false;
                this.ws = null;
                console.log('error');
                resolve(2);
            };
            ws.onclose = () => {
                this.isReady = false;
                this.ws = null;
                console.log('onclose');
                resolve(1);
            };
            this.ws = ws;
        }));
    }
}