
import { defineComponent, ref, watch } from 'vue';

export default defineComponent({
  name: 'cc-checkbox',
  emits: ['update:value', 'change'],
  props: {
    value: {
      type: Boolean,
      default: false,
    },
    label: {
      type: String,
      default: '',
    },
  },
  setup(props, { emit }) {
    const val = ref(props.value);
    watch(() => props.value, (v) => {
      val.value = v;
    });
    return {
      val,
      onChange() {
        const v = val.value;
        emit('update:value', v);
        emit('change', v);
      },
    };
  },
});
